<template>
<div class="card-show">
      <!-- Header  -->
      <component :is="`header`" />
      <div class="content">
          <swiper class="swiper" 
            :breakpoints="swiperOptions.breakpoints"
            :speed="1000"
            navigation
            :pagination="{ clickable: true }">
             <!-- @swiper="onSwiper"
            @slideChange="onSlideChange" -->
            <swiper-slide v-for="item in List" :key="item">
            <div class="portfolio-item">
                     <router-link :to="`/portfolio/${item.id}`" style="color: #6b6c6c;text-decoration: none;">
                    <img :src="`${item.url}`"  />
                    <!-- <i class="far fa-eye"></i> -->
                     </router-link>
                 </div>
            </swiper-slide>
          </swiper> 
      </div>
    </div>
</template>
<style lang="scss" scoped>
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Tajawal:wght@500&display=swap');
//   font-family: 'Dancing Script', cursive;
.card-show{
    position: relative;
    z-index: 2;
    width: 90%;
    margin: 0 auto;
    height: 550px;
    border-radius: 10px;
   
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-color: #000;
    box-shadow: 12px 14px 24px #626060;
   
    .content{
      height: 100%;
      padding: 20px;
      padding-top: 75px;
      overflow: hidden;
      .swiper-container{
        overflow: visible;
        height: 95%;
      .portfolio-item{
        height: 100%;
        border-radius: 5px;
        overflow: hidden;
        img{
          width: 100%;
          transition: all 20s;
          &:hover{
          transform: translatey(calc(-100% + 440px));
        }
        }
      }
      }
      
      
    }
    
  }
</style>
<script>
import { defineAsyncComponent } from "vue";
// swiper
// import Swiper core and required components
import SwiperCore, { Navigation, Pagination, A11y } from "swiper";

// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";

// Import Swiper styles
import "swiper/swiper.scss";
// import "swiper/components/navigation/navigation.scss";
import "swiper/components/pagination/pagination.scss";

// install Swiper components
SwiperCore.use([Navigation, Pagination, A11y]);
export default {
  name: "portfolio",
  data() {
    return {
    List:[
      {
        id: 1,
        url:'img/Portfolio.png',
        name:""
      },
      {
        id: 2,
        url:'img/Portfolio.png',
        name:""
      },
      {
        id: 3,
        url:'img/Portfolio.png',
        name:""
      },
      {
        id: 4,
        url:'img/Portfolio.png',
        name:""
      },
      {
        id: 5,
        url:'img/Portfolio.png',
        name:""
      },
      {
        id: 6,
        url:'img/Portfolio.png',
        name:""
      },
    ],
    swiperOptions: { 
                breakpoints: {
       
        300:{  // when window width from 300px to 576px
            slidesPerView: 1,
            spaceBetween:50
       },
       576:{  // when window width from 576px to 767px
            slidesPerView: 1,
            spaceBetween:50
       },
        767: { // when window width from 767px to 991px
            slidesPerView: 1,
            spaceBetween:50
        },
      
        991: { // when window width from 991px to 1200px
            slidesPerView: 1,
            spaceBetween:50
        },
         1200: { // when window width from 1200px to higher
            slidesPerView: 1,
            spaceBetween:50     
        },
        }
        }
    };
  },

  props: [],
  components: { 
    header: defineAsyncComponent(() =>
      import("./header.vue")
    ),
     Swiper,
    SwiperSlide,
    },
  computed: {},
  methods: {},
   mounted() {
  
  }
};
</script>
